@import "../../../base/fn";

.weui-cells_checkbox {
    .weui-check__label {
        &:before { left: 55px; }
    }
    .weui-cell__hd {
        padding-right: @weuiCellInnerGapH;
        font-size: 0;
    }
    .weui-icon-checked {
